import Slider from '../slider/Slider';
import Cards from '../cards/Cards';
import IntroCard from '../introCard/IntroCard';
import Map from '../map/Map';
import './Home.scss';

function Home() {   
	let slider_data = require('../data/sliders.json'); 
	let cards_data = require('../data/cards.json'); 
	let map_title = "Vous pouvez nous retrouver à cette addresse";
	let map_subtitle = "7 avenue habib bourguiba Mornaguia, Manouba, 1110 Tunisie";
	let card_img = "https://calirextn.com/assets/images/calirex-tunisie-geolocatisation.jpg";
    const cards = [];
    for (let card of cards_data.cards) {
		const card_item = (
			<Cards type={card.type} title={card.title} src={card.src} text_size={card.text_size} text={card.text} alt={(card.alt) ? card.alt : ''}></Cards>
		);
      cards.push(card_item);
    }
    return (
        <> 
			<IntroCard/>
			{cards}
			<Map title={map_title} subtitle={map_subtitle} src={card_img} alt="Calirex Tunisie address" />
        </>  
    );
}
export default Home;
